.Nav{
    position: absolute;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: transparent;
    z-index: 1;
}
.logo-kc{
    position: absolute;
    top: 5px;
    left: 20px;
    height: 50px;
    width: 40px;
}
.logo-cc{
    position: absolute;
    top: 5px;
    right: 20px;
    height: 40px;
    width: 40px;
}
.sfi h1{
    position: absolute;
    left: 9%;
    font-family: 'Lora', serif;
    color: black;
    font-size: 2em;
    text-transform: uppercase;
    font-weight: bold;
    text-shadow: 1px 1px 1px rgb(246, 236, 236);
    margin-top: 0;
    margin-bottom: 0;
    text-decoration: none;
}

@media (min-width: 479px) and (max-width: 768px) {

    .sfi h1 {
       
        left: 17%;
        font-size: 20px;
        margin-top: 18px;
        text-shadow: none;
        margin-bottom: 0;
        text-decoration: none;
    }
      
}
@media (min-width: 400px) and (max-width:479px) {
    .sfi h1 {
       
        left: 16%;
        font-size: 1.0em;
        margin-top: 18px;
        text-shadow: none;
        margin-bottom: 0;
        text-decoration: none;
    }
}
@media (min-width:350px) and (max-width:400px) {
    .sfi h1 {
       
      left: 17%;
    font-size: 13.6px;
    margin-top: 18px;
    text-shadow: none;
    margin-bottom: 0;
    text-decoration: none;
    }
}
@media (min-width: 300px) and (max-width:350px) {
    .logo-kc{
      
        left: 0px;
    }
    .logo-cc{
        
        right: 0px;
    }
    .sfi h1 {
       
        left: 13%;
        font-size: 12.6px;
        margin-top: 18px;
        text-shadow: none;
        margin-bottom: 0;
        text-decoration: none;
    }
}@media (min-width: 20px) and (max-width:300px) {
    .logo-kc{
       
        left: 0px;
    }
    .logo-cc{
      
        right: 0px;
    }
    .sfi h1 {
      
        left: 13%;
        font-size: 10.6px;
        margin-top: 18px;
        text-shadow: none;
        margin-bottom: 0;
        text-decoration: none;
    }
}